.elementor-element .aarambha-kits-badge {
  width: 60px;
  height: 60px;
  display: block;
  margin-left: auto;
  margin-right: auto;
  background-size: 100%;
  background-repeat: no-repeat;
  position: relative;
}
.elementor-element .aarambha-kits-badge:before {
  position: absolute;
  display: block;
  content: "";
  height: 20px;
  width: 20px;
  top: -10px;
  right: -30px;
   /* webpackIgnore: true */
   background-image: url('../../images/logo.png');
   background-size: 100%;
}
